
// Delete style
*,
*::before,
*::after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}
a {
    text-decoration: none;
    color: inherit;
}
ul,
ol,
li {
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
}
img {
    vertical-align: top;
    max-width: 100%;
}
p,
span {
    margin: 0;
    font-weight: inherit;
    font-size: inherit;
}
hr {
    border: 1px solid #EDECEC;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
    margin: 0;
}
html,
body {
    height: 100%;
    line-height: 1;
    color: #000;
    font-family: 'Arial';
    font-style: normal;
    font-weight: 400;
}
button,
input {
    font-weight: inherit;
    font-size: inherit;
    color: inherit;
    font-family: inherit;
}
button {
    cursor: pointer;
    background-color: inherit;
}

// Color styles --------
// ----background
$accent: rgba(255, 242, 0, 0.7);
$background: #FFFFFF;
$color-bg-secondary: #F7F7F7;
// ----text
$color-text:#000000;
$color-text-secondary: #777777;
$hover: #CB0000;
// ----separators 
$border:#E0E0E0;
// ----basiccolors 
$color-primary:#008BAA;
$color-secondary:#FF9900;

[class*="__container"] {
    max-width: 1040px;
    margin: 0 auto;
}
section {
    margin-top: 24px;
}
.main {
    flex-grow: 1;
}
.wrapper {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    .header {
        display: flex;
        height: 104px;
        background: $color-primary;
        color: $background;
        &__container {
            width: 1040px;
            display: flex;
            align-items: center;
            gap: 20px;
        }
        &__menu {
            flex: 1 1 auto;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .text {
                p {
                    font-size: 16px;
                    line-height: 18px;
                }
            }
            .phone {
                display: flex;
                align-items: center;
                gap: 8px;
                span {
                    font-size: 30px;
                    line-height: 34px;
                }
            }
            .social {
                display: flex;
                gap: 16px;
                img {
                    cursor: pointer;
                    &:hover {
                        opacity: 80%;
                        transition: 0.2s;
                    }
                }
            }
        }
    }
    .main {
        .page {
            margin-top: 25px;
            &__image {
                display: flex;
               
                align-items: flex-end;
                gap: 50%;
                // position: relative;
                background: url("../images/page/background.jpg") center no-repeat;
                width: 1040px;
                height: 553px;
                .ellipse {
                    // position: absolute;
                    margin-left: 30px;
                    margin-bottom: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    gap: 16px;
                    width: 250px;
                    height: 250px;
                    background: rgba(255, 242, 0, 0.7);
                    border-radius: 50%;
                    &-top {
                        width: 169px;
                        height: 88px;
                        h3 {
                            color: $color-text;
                            font-family: 'Trebuchet MS';
                            font-style: normal;
                            font-weight: 700;
                            font-size: 25px;
                            line-height: 29px;
                            text-align: center;
                        }
                    }
                    &-bottom {
                        display: flex;
                        flex-direction: column;
                        gap: 6px;
                        p {
                            color: $color-text;
                            font-family: 'Trebuchet MS';
                            font-style: normal;
                            font-weight: 700;
                            font-size: 14px;
                            line-height: 16px;
                            text-align: center;
                            text-transform: uppercase;
                        }
                        span {
                            font-family: 'Trebuchet MS';
                            font-size: 24px;
                            line-height: 28px;
                            text-align: center;
                            text-transform: uppercase;
                            color: $background;
                            background: $color-text;
                            padding: 0px 5px;
                        }
                    }
                }
                .factoid {
                    // position: absolute;
                    width: 144px;
                    margin-bottom: -140px;
                    // bottom: -124px;
                    // right: 100px;
                    span {
                        color: $color-text-secondary;
                        font-family: 'Trebuchet MS';
                        font-size: 88px;
                        line-height: 102px;
                        letter-spacing: -0.05em;
                        text-transform: uppercase;
                    }
                    p {
                        color: $color-text;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 30px;
                        line-height: 31px;
                    }
                }
            }
            .rating {
                display: flex;
                &__right {
                    margin-top: 30px;
                    &-top {
                        display: flex;
                        align-items: center;
                        gap: 20px;
                        h2 {
                            font-family: 'Trebuchet MS';
                            font-style: normal;
                            font-weight: 700;
                            font-size: 35px;
                            line-height: 110%;
                            border-bottom: 1.9px solid #34567657
                        }
                        span {
                            font-family: 'Trebuchet MS';
                            font-style: normal;
                            font-weight: 700;
                            font-size: 35px;
                            line-height: 130%;
                        }
                    }
                    &-bottom {
                        margin-top: 27px;
                        display: flex;
                        align-items: center;
                        gap: 30px;
                        .respons {
                            p {
                                font-style: normal;
                                font-weight: 400;
                                font-size: 14px;
                                line-height: 16px;
                            }
                            span {
                                font-style: normal;
                                font-weight: 700;
                                font-size: 14px;
                                line-height: 21px;
                                letter-spacing: 0.05em;
                            }
                        }
                        .regtangle {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            background: $color-primary;
                            width: 46px;
                            height: 48px;
                            font-family: 'Trebuchet MS';
                            font-style: normal;
                            font-weight: 700;
                            font-size: 24px;
                            line-height: 28px;
                            text-align: center;
                            color: $background;
                        }
                        .list {
                            display: flex;
                            color: $color-text;
                            gap: 16px;
                            p {
                                font-size: 12px;
                                line-height: 14px;
                                letter-spacing: 0.05em;
                                text-transform: uppercase;
                            }
                            span {
                                font-style: normal;
                                font-weight: 700;
                                font-size: 18px;
                                line-height: 21px;
                                letter-spacing: 0.05em;
                                text-transform: uppercase;
                            }
                        }
                    }
                }
               
            }
        }
        .hand {
            margin-top: 50px;
           &__content {
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                gap: 40px;
                .card {
                    &__text {
                        margin-top: 25px;
                        .top {
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            h2 {
                                font-family: 'Trebuchet MS';
                                font-style: normal;
                                font-weight: 700;
                                font-size: 35px;
                                line-height: 110%;
                                border-bottom: 1.9px solid #34567657;
                                &:hover {
                                    cursor: pointer;
                                    transition: 0.2s;
                                    color: $hover;
                                    border-bottom: 1.9px solid #e0a5a5;
                                }


                            }
                            span {
                                font-family: 'Trebuchet MS';
                                font-style: normal;
                                font-weight: 700;
                                font-size: 35px;
                                line-height: 130%;
                            }
                          
                        }
                       .bottom {
                            margin-top: 10px;
                            display: flex;
                            justify-content: space-between;
                            p {
                                font-style: normal;
                                font-weight: 400;
                                font-size: 14px;
                                line-height: 19px;
                            }
                            .text {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                span {
                                    position: relative;
                                    font-family: 'Trebuchet MS';
                                    font-size: 20px;
                                    line-height: 23px;
                                    color: $color-text-secondary;
                                }
                                .line {
                                    position: absolute;
                                    border: 1px solid #CB0000;
                                    width: 38px;
                                }
                            }
                        }
                    }
                }
           }
        }
        .rectangle {
            margin-top: 50px;
            &__content {
                display: flex;
                flex-direction: column;
                justify-content: center;
                .code {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    justify-content: space-evenly;
                    gap: 10px;
                    padding: 10px;
                    min-height: 149px;
                    border: 1px solid #E0E0E0;
                    border-top-left-radius: 6px;
                    border-top-right-radius: 6px;
                    &-left {
                        h2 {
                            color: $color-primary;
                            font-style: normal;
                            font-weight: 700;
                            font-size: 28px;
                            line-height: 120%;
                        }
                    }
                    &-center {
                        span {
                            color: $color-primary;
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 120%;
                            letter-spacing: 1px;
                            text-transform: uppercase;
                        }
                        h2 {
                            color: $color-text;
                            font-style: normal;
                            font-weight: 700;
                            font-size: 22px;
                            line-height: 130%;
                        }
                        p {
                            color: $color-text;
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 19px;
                        }
                    }
                    &-right {
                        .btn {
                            box-sizing: border-box;
                            display: flex;
                            flex-direction: row;
                            justify-content: center;
                            align-items: center;
                            gap: 8px;
                            width: 224px;
                            height: 47px;
                            background: linear-gradient(180deg, #00AAAA 0%, #008BAA 100%);
                            border-radius: 24px;
                            &:hover {
                                opacity: 90%;
                                transition: 0.2s;
                            }
                        }
                    }
                }
                .details {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    min-height: 47px;
                    gap: 10px;
                    border: 1px solid #E0E0E0;
                    border-bottom-right-radius: 6px;
                    border-bottom-left-radius: 6px;
                    background: $color-bg-secondary;
                    p {
                        color: $color-text-secondary;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 13px;
                        line-height: 120%;
                    }
                    img {
                        cursor: pointer;
                    }
                }
            }
        }
        .create {
            margin-top: 65px;
            &__content {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                gap: 35px;
                .left {
                    width: 792px;
                    &-title {
                        margin-bottom: 33px;
                        h1 {
                            color: $color-text;
                            font-family: 'Trebuchet MS';
                            font-style: normal;
                            font-weight: 700;
                            font-size: 48px;
                            line-height: 130%;
                        }
                    }
                    &-text {
                        span {
                            color: $color-text;
                            font-style: normal;
                            font-weight: 700;
                            font-size: 14px;
                            line-height: 19px;
                        }
                        p.top {
                            margin-top: 10px;
                            
                        }
                        p.frame {
                            border-left: 8px solid $color-primary;
                            padding: 8px;
                            margin-left: 83px;
                        }
                        p {
                            color: $color-text;
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 19px;
                            margin-bottom: 25px;
                        }
                        h2 {
                            color: $color-text;
                            font-style: normal;
                            font-weight: 700;
                            font-size: 22px;
                            line-height: 130%;
                            margin-bottom: 8px;
                        }
                        h3 {
                            color: $color-text;
                            font-style: normal;
                            font-weight: 700;
                            font-size: 16px;
                            line-height: 130%;
                            margin-bottom: 8px;
                        }
                    }
                    &-table {
                        max-width: 776px;
                        padding-left: 20px;
                        table {
                            border-collapse: collapse; 
                            background:white;
                            overflow:hidden;
                            margin:0 auto;
                            width: 100%;
                            td {
                                padding: 8px;
                                height: 64px;
                            }
                            thead {
                                tr {
                                    td {
                                        width: 25%;
                                    color: $color-text;
                                    background: #F7F7F7;;
                                    font-family: 'DM Sans';
                                    font-style: normal;
                                    font-weight: 700;
                                    font-size: 14px;
                                    line-height: 20px;
                                    letter-spacing: 0.1px;
                                    }
                                }
                              
                            }
                            tbody {
                               tr {
                                   border-bottom: 1.9px solid  #E5E5E5;;
                                    &:last-child {
                                        border-bottom: none;
                                    }
                                    td {
                                        color: $color-text;
                                        font-family: 'DM Sans';
                                        font-style: normal;
                                        font-weight: 400;
                                        font-size: 14px;
                                        line-height: 20px;
                                        letter-spacing: 0.1px;
                                    }
                                }
                            }
                        }
                    }
                    &-list {
                        p {
                            color: $color-text;
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 19px;
                        }
                        ul {
                            margin-left: 40px;
                            li {
                                color: $color-text;
                                padding-top: 5px;
                                list-style: disc;
                                font-style: normal;
                                font-weight: 400;
                                font-size: 14px;
                                line-height: 19px;
                            }
                        }
                    }
                }
                .right {
                    width: 205px;
                    // padding-top: 140px;
                    h2 {
                        color: $color-text-secondary;
                        font-family: 'Trebuchet MS';
                        font-size: 100px;
                        line-height: 116px;
                        letter-spacing: -0.05em;
                    }
                    span {
                        color: $color-text;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 30px;
                        line-height: 31px;
                    }
                    p {
                        color: $color-text-secondary;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 20px;
                        line-height: 25px;
                    }
                }
            }
        }
    }
    .footer {
        margin-top: 50px;
        margin-bottom: 20px;
        &__content {
            margin-top: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .text {
                display: flex;
                gap: 10px;
                p {
                    color: $color-text;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 17px;
                }
                a {
                    cursor: pointer;
                    color: $color-primary;
                }
            }
            .image {
                cursor: pointer;
                display: flex;
                gap: 10px;
                img {
                    &:hover {
                        opacity: 80%;
                        transition: 0.2s;
                    }
                }
            }
        }
    }
}